@import "./src/styles/mixins";

.header {
  width: 100%;
  box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);

  .headerWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1440px;
    margin: 0 auto;
    padding: 6px 10px;
    background: #FCFCFC;
  }

  .account {
    width: 113px;

    button {
      height: 36px;
      border: none;
      border-radius: 30px;
      @include buttonTitle();
    }
  }

  .accountPhoto {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;


    img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
    }

    span {
      font-weight: 500;
      font-size: 16px;
      margin-right: 12px;
      border-bottom: 1px;
      border-bottom-style: dashed;
      border-bottom-color: black;
    }
  }
}


@media (max-width: 500px) {
  .headerWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1280px;
    margin: 0 auto;
    padding: 6px 10px;
    background: #FCFCFC;

    div {
      img {
        width: 170px;
      }
    }
  }
}